body {
  margin: 0 !important;
  padding: 0 !important;
  height: 100%;
  font-family: 'Space Mono', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html, main {
  margin: 0;
  padding: 0;
  height: 100%;
}

.content-wrapper {
  padding: 5em;
}

@media only screen and (max-width: 600px) {
  .content-wrapper {
    padding: 0;
    padding-top: 5em;
  }
}

.link-no-style {
  text-decoration: none;
  color: #ecf0f1;
}

#root {
  height: 100%;
}

@-webkit-keyframes spin-right {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes spin-right {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

.button-reversed {
  border: black dashed 2px;
  background-color: transparent;
  color: black;
  font-family: 'Space Mono', monospace;
  font-size: 1em;
  padding: .5em;
  margin: .5em;
}

.buttonReversed:hover {
  border: black 2px outset;
  cursor: pointer;
}

button {
  border: #ecf0f1 dashed 2px;
  background-color: transparent;
  color: #ecf0f1;
  font-family: 'Space Mono', monospace;
  font-size: 1em;
  padding: .5em;
}

#button-style {
  border: #ecf0f1 1px outset;
  background-color: transparent;
  text-decoration: none;
  color: #ecf0f1;
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 14px;
  padding: 1em 2em 1em 2em;
}

button:hover {
  border: #ecf0f1 2px outset;
  cursor: pointer;
}

#button-style:hover {
  border: #ecf0f1 dashed 1px;
  cursor: pointer;
}

button:focus {
  outline: 0;
}

.clearButton {
  border: none;
  display: -webkit-flex;
  display: flex;
  margin: 0;
  padding: 0;
}

.clearButton:hover {
  border: none;
  margin: 0;
  padding: 0;
}

input {
  border: #ecf0f1 dashed 2px;
  text-decoration: none;
  font-family: 'Space Mono', monospace;
  font-size: 1em;
  padding: .5em;
}
.header {
  display: -webkit-flex;
  display: flex;
  padding: 1.5em;
  position: fixed;
  width: 100%;
  z-index: 15;
}

.earth {
  height: 24px;
  padding-right: 1em;
}

.left {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.right {
  margin-left: auto;
  margin-right: 3em;
}

.nav .nav-content {
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  height: 100%;
}

.nav-item {
  display: inline;
  margin-left: 2rem;
  color: #fff;
}

.nav-item:hover {
  text-decoration: underline;
  cursor: pointer;
}

.nav-item-home {
  display: -webkit-flex;
  display: flex;
  color: #fff;
}

.nav-item-home:hover, .nav-item-home-text:hover {
  text-decoration: underline;
  cursor: pointer;
}

